#lang-menu .lang-menu-item {
  display: flex;
  align-items: center;
}
#lang-menu .lang-menu-item:hover {
  cursor: pointer;
}
#lang-menu .img-flag {
  width: 30px;
  height: 30px;
}
#lang-menu .left-img {
  margin-right: 10px;
}
#lang-menu .img-options {
  width: 18px;
  height: 18px;
  margin-left: 10px;
}
#lang-menu .display-langs {
  border: none;
  background: none;
  padding: 0;
  margin: 0;
}
#lang-menu .menu-item-options {
  background-color: #26262a;
  border: solid 2px #3e3e45;
  border-radius: 20px;
  position: absolute;
  width: 38px;
  padding: 2px;
  top: 35px;
}
#lang-menu .menu-item-options:hover {
  cursor: pointer;
}
#lang-menu .lang-menu-options-visible {
  display: flex;
  justify-content: space-between;
}
#lang-menu .lang-menu-options-hide {
  display: none;
}
@media (min-width: 601px) {
  #lang-menu .left-img {
    margin-right: 5px;
  }
  #lang-menu .img-flag {
    width: 20px;
    height: 20px;
  }
  #lang-menu .img-options {
    width: 15px;
    height: 15px;
    margin-left: 3px;
  }
  #lang-menu .menu-item-options {
    top: 32px;
    width: 29px;
  }
}
