.section-header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.section-header .page-title {
  width: 90%;
  font-size: 1.8em;
}
.section-header hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #3e3e45;
  padding: 0;
  width: 100%;
}
.section-header img {
  margin-right: 10px;
  height: 15px;
  width: 15px;
}
.section-header img:hover,
.section-header .page-title:hover {
  cursor: pointer;
}
