.social-icons {
  display: flex;
  align-items: center;
  padding-top: 4px;
}
.social-icons a {
  margin-left: 15px;
}
.social-icons a img {
  width: 35px;
}
@media (min-width: 601px) {
  .social-icons {
    margin-top: 4px;
    padding: 0;
    width: unset;
  }
  .social-icons a {
    margin-left: 8px;
  }
}
