#softSkills ul li {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}
#softSkills ul li .title {
  margin-bottom: 5px;
}
#softSkills ul li .div-img {
  margin-right: 10px;
  display: flex;
  align-items: center;
}
#softSkills ul li img {
  height: 100px;
}
@media (min-width: 701px) {
  #softSkills ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  #softSkills ul li {
    width: 48%;
    display: flex;
    margin-bottom: 20px;
  }
  #softSkills ul li .title {
    margin-bottom: 5px;
  }
  #softSkills ul li .div-img {
    margin-right: 10px;
    display: block;
  }
  #softSkills ul li img {
    height: 60px;
  }
}
